body {
  margin: 0;
  font-family: 'Aileron', sans-serif;
  font-weight: 100;
  background-color: lighten(pink, 10%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Aileron';
  src: local('Aileron'), url('./assets/Aileron/Aileron-Black.ttf') format('truetype');
  font-weight: normal;
}

@font-face {
  font-family: 'Aileron';
  src: local('Aileron'), url('./assets/Aileron/Aileron-Bold.ttf') format('truetype');
  font-weight: bold;
}

@font-face {
  font-family: 'Aileron';
  src: local('Aileron'), url('./assets/Aileron/Aileron-Italic.ttf') format('truetype');
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: 'Aileron';
  src: local('Aileron'), url('./assets/Aileron/Aileron-Light.ttf') format('truetype');
  font-weight: lighter;
  font-style: normal;
}
